import { getAdminPackages } from '@/actions/package';
import { PackageActions } from '@/components/PackageActions';
import Link from 'next/link';


export default async function AdminPackagesPage() {
  const packages = await getAdminPackages();

  return (
    <div className="space-y-6">
      <header className="flex flex-col sm:flex-row sm:items-center justify-between gap-4 border-b border-white/5 pb-5">
        <div>
          <h1 className="text-3xl font-bold">Manajemen Paket</h1>
          <p className="text-neutral-400 mt-1">Kelola semua paket gift skin yang tersedia</p>
        </div>
        <Link
          href="/admin/packages/new"
          className="inline-flex items-center gap-2 px-5 py-2.5 bg-blue-500 hover:bg-blue-400 text-neutral-950 font-bold rounded-xl transition-all shadow-lg shadow-blue-500/20 shrink-0"
        >
          <i className="fa-solid fa-plus"></i> Tambah Paket
        </Link>
      </header>

      {packages.length === 0 ? (
        <div className="flex flex-col items-center justify-center py-24 text-center bg-[#0d0f17] border border-dashed border-neutral-700 rounded-2xl">
          <div className="w-16 h-16 rounded-2xl bg-neutral-800 flex items-center justify-center mb-4">
            <i className="fa-solid fa-box-open text-2xl text-neutral-500"></i>
          </div>
          <h3 className="font-bold text-lg mb-2">Belum ada paket</h3>
          <p className="text-neutral-500 mb-6">Tambahkan paket pertama anda untuk ditampilkan di halaman utama.</p>
          <Link href="/admin/packages/new" className="px-5 py-2.5 bg-blue-500 hover:bg-blue-400 text-neutral-950 font-bold rounded-xl transition-all">
            <i className="fa-solid fa-plus mr-2"></i>Tambah Paket Pertama
          </Link>
        </div>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 xl:grid-cols-3 gap-5">
          {packages.map((pkg) => (
            <div
              key={pkg.id}
              className={`bg-[#0d0f17] border rounded-2xl overflow-hidden transition-all group ${
                pkg.isActive ? 'border-white/5 hover:border-white/10' : 'border-white/5 opacity-60'
              }`}
            >
              {/* Image */}
              <div className="relative h-44 bg-neutral-800 overflow-hidden">
                {pkg.imageUrl ? (
                  // eslint-disable-next-line @next/next/no-img-element
                  <img src={pkg.imageUrl} alt={pkg.name} className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-500" />
                ) : (
                  <div className="w-full h-full flex items-center justify-center">
                    <i className="fa-solid fa-image text-4xl text-neutral-700"></i>
                  </div>
                )}
                <div className={`absolute top-3 right-3 text-xs font-bold px-2.5 py-1 rounded-full backdrop-blur-sm ${
                  pkg.isActive
                    ? 'bg-emerald-500/20 text-emerald-400 border border-emerald-500/30'
                    : 'bg-neutral-700 text-neutral-400 border border-neutral-600'
                }`}>
                  {pkg.isActive ? 'Aktif' : 'Nonaktif'}
                </div>
                <div className="absolute top-3 left-3 text-xs font-semibold px-2.5 py-1 rounded-full bg-black/60 text-neutral-300 backdrop-blur-sm">
                  {pkg.category}
                </div>
              </div>

              {/* Content */}
              <div className="p-5">
                <div className="flex items-start justify-between gap-2 mb-1">
                  <h3 className="font-bold text-lg leading-tight">{pkg.name}</h3>
                  <span className="text-amber-400 font-bold text-sm shrink-0">{pkg.price}</span>
                </div>
                {pkg.description && (
                  <p className="text-neutral-500 text-sm mt-1 line-clamp-2">{pkg.description}</p>
                )}
                <div className="flex items-center gap-3 mt-3 text-sm">
                  <span className={`flex items-center gap-1.5 font-semibold ${
                    pkg.stock === 0 ? 'text-red-400' : pkg.stock <= 5 ? 'text-amber-400' : 'text-emerald-400'
                  }`}>
                    <i className="fa-solid fa-boxes-stacked text-xs"></i>
                    Stok: {pkg.stock}
                  </span>
                  <span className="text-neutral-700">•</span>
                  <span className="text-neutral-500 text-sm">
                    <i className="fa-solid fa-shopping-cart text-xs mr-1"></i>
                    {pkg._count.orders} order
                  </span>
                </div>

                {/* Actions — Client Component */}
                <PackageActions id={pkg.id} name={pkg.name} isActive={pkg.isActive} />
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
